.detail {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 3rem 10vw;
    &__image {
        width: 100%;
    }
    &__title {
        display: flex;
        width: 100%;
        padding-bottom: 1rem;
        align-items: flex-start;
        font-size: 2rem;
        border-bottom: 1px solid black;
    }
    &__price {
        width: 100%;
        font-size: 1.3rem;
        margin: 1rem 0;
    }
    &__description {
        width: 100%;
        margin: 1rem 0;
    }
    &__cart {
        width: 100%;
        color: #d87093;
        text-decoration: none;
        text-transform: uppercase;
        height: 3rem;
        background-color: white;
        border: 1px solid #d87093;
        border-radius: 25px;
        font-size: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        &:hover {
            background-color: #d87093;
            border-color: #d87093;
            color: white;
            transition: 0.5s linear;
        }
    }
}

@media only screen and (min-width: 990px) {
    .detail__info {
        padding-left: 1.5rem;
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .detail__image-container {
        width: 50%;
    }
}

@media only screen and (max-width:989px) {
    .detail__info {
        width: 100%;
    }
}